<script lang="ts">
  import { onMount } from "svelte";
  import { nimbus } from "~/lib/network";
  import mixpanel from "mixpanel-browser";
  import { useNavigate } from "svelte-navigator";
  import { createQuery } from "@tanstack/svelte-query";
  import { isDarkMode, selectedPackage, typeWallet, chain } from "~/store";
  import { filterDuplicates } from "~/utils/index";
  import { chainList, chainMoveList } from "~/lib/chains";
  import { getTokenInfo } from "~/lib/queryAPI";
  import { t } from "~/lib/i18n";
  import { isNimbusVersion } from "~/utils/constants";

  import Table from "~/components/Table.svelte";
  import LoadingPremium from "~/components/LoadingPremium.svelte";
  import ErrorBoundary from "~/components/ErrorBoundary.svelte";
  import Button from "~/components/Button.svelte";
  import TooltipTitle from "~/components/TooltipTitle.svelte";
  import Loading from "~/components/Loading.svelte";
  import Select from "~/components/Select.svelte";
  import Pagination from "~/components/Pagination.svelte";
  import PublicPortfolioItem from "~/UI/WhalesList/PublicPortfolioItem.svelte";
  import PublicPortfolioMobileItem from "~/UI/WhalesList/PublicPortfolioMobileItem.svelte";

  import Ton from "~/assets/chains/ton.png";

  const navigate = useNavigate();

  const LIST_CHAINS = new Set(["SUI", "BASE", "ETH"]);

  const formatChainList: any = [
    ...chainMoveList.filter((item) => LIST_CHAINS.has(item.value)),
    // ...chainList.filter((item) => LIST_CHAINS.has(item.value)),
    // {
    //   logo: Ton,
    //   label: "Ton",
    //   value: "TON",
    // },
  ];

  let whalesData: any = [];

  let orderBy = "pnl30d:desc";
  let limit = 50;
  let pagination = 1;
  let totalPage = 0;

  let whalePageChain = "";
  let addressTokens = "";

  onMount(() => {
    mixpanel.track("market_page");
    const urlParams = new URLSearchParams(window.location.search);
    const chainParams = urlParams.get("chain");
    if (chainParams && chainParams?.length !== 0) {
      whalePageChain = chainParams;
    } else {
      whalePageChain = "SUI";

      // if ($typeWallet === "MOVE") {
      //   whalePageChain = "SUI";
      // } else if ($typeWallet === "EVM" && $chain !== "ALL") {
      //   whalePageChain = $chain;
      // } else if ($typeWallet === "TON") {
      //   whalePageChain = "TON";
      // } else {
      //   whalePageChain =
      //     formatChainList?.find((item: any) => item.value === "ETH").value ||
      //     "ETH";
      // }
    }
  });

  $: {
    if (whalePageChain) {
      window.history.replaceState(
        null,
        "",
        `${window.location.pathname}?chain=${whalePageChain}`,
      );
    }
  }

  const getPublicPortfolio = async (
    chain: String,
    orderBy: String,
    limit: Number,
    pagination: Number,
  ) => {
    const response: any = await nimbus
      .get(
        `/whales?chain=${chain}&orderBy=${orderBy}&limit=${limit}&page=${pagination}`,
      )
      .then((res: any) => res?.data?.result);
    return response;
  };

  $: queryWhalesList = createQuery({
    queryKey: ["whaleslist", whalePageChain, orderBy, limit, pagination],
    queryFn: () => {
      return getPublicPortfolio(whalePageChain, orderBy, limit, pagination);
    },
    enabled: Boolean(whalePageChain && whalePageChain.length !== 0),
  });

  $: queryTokensList = createQuery({
    queryKey: ["tokenInfo", addressTokens, whalePageChain],
    queryFn: () => {
      return getTokenInfo(addressTokens, whalePageChain);
    },
    enabled: Boolean(addressTokens && addressTokens.length !== 0),
  });

  const handlePageChange = (page: number) => {
    pagination = page;
  };

  const toggleSortWhalesList = (key: any) => {
    let orderKey = orderBy.split(":")[0];
    let orderType = orderBy.split(":")[1];
    if (key === orderKey) {
      if (orderType === "default") {
        orderType = "asc";
      } else if (orderType === "asc") {
        orderType = "desc";
      } else if (orderType === "desc") {
        orderType = "default";
      } else {
        orderType = "default";
      }
    } else {
      orderKey = key;
      orderType = "asc";
    }
    orderBy = `${orderKey}:${orderType}`;
  };

  $: {
    if (!$queryWhalesList.isError && $queryWhalesList?.data) {
      whalesData = $queryWhalesList?.data?.listItems || [];
      totalPage = $queryWhalesList?.data?.totalPages;
      if (whalesData && whalesData.length !== 0) {
        addressTokens = whalesData
          ?.map((item: any) =>
            item?.tokens7d
              ?.slice(0, 3)
              ?.filter((item: any) => item.length > 0)
              ?.join(","),
          )
          ?.join(",");
      }
    }
  }

  $: {
    if (
      $queryTokensList.data &&
      $queryTokensList.data?.length !== 0 &&
      whalesData &&
      whalesData.length !== 0
    ) {
      whalesData = whalesData.map((item: any) => {
        return {
          ...item,
          tokens7d: item?.tokens7d?.map((token: any) => {
            const selectedData = $queryTokensList?.data?.find(
              (item: any) => item?.contract_address === token,
            );
            if (selectedData) {
              return selectedData;
            }
            return {
              contract_address: token,
              image_url: `${import.meta.env.VITE_TOKEN_LOGO}/v1/logo?address=${token}&chain=${whalePageChain}`,
            };
          }),
        };
      });
    }
  }

  $: sortIcon = (sortType: any) => {
    return `<svg
              height="0.9rem"
              viewBox="0 0 24 24"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <g id="SVGRepo_bgCarrier" stroke-width="0" />
              <g
                id="SVGRepo_tracerCarrier"
                stroke-linecap="round"
                stroke-linejoin="round"
              />
              <g id="SVGRepo_iconCarrier">
                <path
                  d="M16.0686 15H7.9313C7.32548 15 7.02257 15 6.88231 15.1198C6.76061 15.2238 6.69602 15.3797 6.70858 15.5393C6.72305 15.7232 6.93724 15.9374 7.36561 16.3657L11.4342 20.4344C11.6323 20.6324 11.7313 20.7314 11.8454 20.7685C11.9458 20.8011 12.054 20.8011 12.1544 20.7685C12.2686 20.7314 12.3676 20.6324 12.5656 20.4344L16.6342 16.3657C17.0626 15.9374 17.2768 15.7232 17.2913 15.5393C17.3038 15.3797 17.2392 15.2238 17.1175 15.1198C16.9773 15 16.6744 15 16.0686 15Z"
                  stroke="${$isDarkMode ? "#ffffff" : "#000000"}"
                  fill="${
                    sortType === "default" || sortType === "desc"
                      ? $isDarkMode
                        ? "#ffffff"
                        : "#000000"
                      : ""
                  }"
                  stroke-width="2"
                  stroke-linecap="round"
                  stroke-linejoin="round"
                />
                <path
                  d="M7.9313 9.00005H16.0686C16.6744 9.00005 16.9773 9.00005 17.1175 8.88025C17.2393 8.7763 17.3038 8.62038 17.2913 8.46082C17.2768 8.27693 17.0626 8.06274 16.6342 7.63436L12.5656 3.56573C12.3676 3.36772 12.2686 3.26872 12.1544 3.23163C12.054 3.199 11.9458 3.199 11.8454 3.23163C11.7313 3.26872 11.6323 3.36772 11.4342 3.56573L7.36561 7.63436C6.93724 8.06273 6.72305 8.27693 6.70858 8.46082C6.69602 8.62038 6.76061 8.7763 6.88231 8.88025C7.02257 9.00005 7.32548 9.00005 7.9313 9.00005Z"
                  stroke="${$isDarkMode ? "#ffffff" : "#000000"}"
                  fill="${
                    sortType === "default" || sortType === "asc"
                      ? $isDarkMode
                        ? "#ffffff"
                        : "#000000"
                      : ""
                  }"
                  stroke-width="2"
                  stroke-linecap="round"
                  stroke-linejoin="round"
                />
              </g>
            </svg>`;
  };
</script>

<ErrorBoundary>
  <div class="header-container">
    <div
      class="flex md:flex-row flex-col justify-between md:items-end items-start gap-4 max-w-[1600px] m-auto xl:w-[82%] w-[94%]"
    >
      <div class="flex flex-col gap-2">
        <div class="text-4xl font-semibold text-white">{$t("Whale")} üê≥</div>
        <div class="text-xl text-white">
          {$t("Start earning")}
        </div>
      </div>
      <div class="md:hidden block">
        <Select
          type="whales-chain"
          positionSelectList="left-0"
          listSelect={formatChainList.sort((a, b) =>
            a.label.localeCompare(b.label),
          )}
          bind:selected={whalePageChain}
          isDisabledSelectWallet={false}
          indexWalletDisabled={-1}
        />
      </div>
      <div class="md:block hidden">
        <Select
          type="whales-chain"
          positionSelectList="right-0"
          listSelect={formatChainList.sort((a, b) =>
            a.label.localeCompare(b.label),
          )}
          bind:selected={whalePageChain}
          isDisabledSelectWallet={false}
          indexWalletDisabled={-1}
        />
      </div>
    </div>
  </div>

  <div
    class="xl:min-h-screen max-w-[1600px] m-auto md:w-[94%] w-full md:-mt-26 -mt-28"
  >
    <div
      class="virtual_portfolio_container rounded-[20px] xl:p-8 p-4 xl:shadow-md flex flex-col gap-5"
    >
      <div class="flex flex-col gap-3">
        <div class="text-sm">Data is updated every 5 minutes</div>
        <Table>
          <span slot="desktop">
            <table class="table-auto w-full h-full">
              <thead>
                <tr class="bg_f4f5f8">
                  <th class="pl-3 py-3 rounded-tl-[10px]">
                    <div class="text-left text-xs uppercase font-medium">
                      {$t("Address")}
                    </div>
                  </th>

                  <th class="py-3">
                    <div
                      on:click={() => {
                        toggleSortWhalesList("pnl1d");
                      }}
                      class="flex items-center justify-end gap-2 text-right text-xs uppercase font-medium cursor-pointer"
                    >
                      1{$t("D")} Pnl
                      <div>
                        {@html sortIcon(
                          orderBy.split(":")[0] === "pnl1d"
                            ? orderBy.split(":")[1]
                            : "default",
                        )}
                      </div>
                    </div>
                  </th>

                  <th class="py-3">
                    <div
                      on:click={() => {
                        toggleSortWhalesList("pnl7d");
                      }}
                      class="flex items-center justify-end gap-2 text-right text-xs uppercase font-medium cursor-pointer"
                    >
                      7{$t("D")} Pnl
                      <div>
                        {@html sortIcon(
                          orderBy.split(":")[0] === "pnl7d"
                            ? orderBy.split(":")[1]
                            : "default",
                        )}
                      </div>
                    </div>
                  </th>

                  <th class="py-3">
                    <div
                      on:click={() => {
                        toggleSortWhalesList("pnl30d");
                      }}
                      class="flex items-center justify-end gap-2 text-right text-xs uppercase font-medium cursor-pointer"
                    >
                      30{$t("D")} Pnl
                      <div>
                        {@html sortIcon(
                          orderBy.split(":")[0] === "pnl30d"
                            ? orderBy.split(":")[1]
                            : "default",
                        )}
                      </div>
                    </div>
                  </th>

                  <th class="py-3">
                    <div
                      on:click={() => {
                        toggleSortWhalesList("profitlf");
                      }}
                      class="flex items-center justify-end gap-2 text-right text-xs uppercase font-medium cursor-pointer"
                    >
                      {$t("Realized Profit")}
                      <div>
                        {@html sortIcon(
                          orderBy.split(":")[0] === "profitlf"
                            ? orderBy.split(":")[1]
                            : "default",
                        )}
                      </div>
                    </div>
                  </th>

                  <th class="py-3">
                    <div
                      on:click={() => {
                        toggleSortWhalesList("total_trades30d");
                      }}
                      class="flex items-center justify-end gap-2 text-right text-xs uppercase font-medium cursor-pointer"
                    >
                      30{$t("D")} TXs
                      <div>
                        {@html sortIcon(
                          orderBy.split(":")[0] === "total_trades30d"
                            ? orderBy.split(":")[1]
                            : "default",
                        )}
                      </div>
                    </div>
                  </th>

                  <th class="py-3">
                    <div
                      on:click={() => {
                        toggleSortWhalesList("avg_holding_time");
                      }}
                      class="flex items-center justify-end gap-1 text-right text-xs uppercase font-medium cursor-pointer"
                    >
                      <TooltipTitle tooltipText={$t("Average Holding Time")}>
                        AHT
                      </TooltipTitle>
                      <div>
                        {@html sortIcon(
                          orderBy.split(":")[0] === "avg_holding_time"
                            ? orderBy.split(":")[1]
                            : "default",
                        )}
                      </div>
                    </div>
                  </th>

                  <th class="py-3">
                    <div
                      on:click={() => {
                        toggleSortWhalesList("avg_total_vol");
                      }}
                      class="flex items-center justify-end gap-1 text-right text-xs uppercase font-medium cursor-pointer"
                    >
                      <TooltipTitle tooltipText={$t("Average Trade Volume")}>
                        ATV
                      </TooltipTitle>
                      <div>
                        {@html sortIcon(
                          orderBy.split(":")[0] === "avg_total_vol"
                            ? orderBy.split(":")[1]
                            : "default",
                        )}
                      </div>
                    </div>
                  </th>

                  <th class="py-3">
                    <div
                      on:click={() => {
                        toggleSortWhalesList("last_trade");
                      }}
                      class="flex items-center justify-end gap-2 text-right text-xs uppercase font-medium cursor-pointer"
                    >
                      {$t("Last Action")}
                      <div>
                        {@html sortIcon(
                          orderBy.split(":")[0] === "last_trade"
                            ? orderBy.split(":")[1]
                            : "default",
                        )}
                      </div>
                    </div>
                  </th>

                  <th class="pr-3 py-3 rounded-tr-[10px]">
                    <div class="text-right text-xs uppercase font-medium">
                      <TooltipTitle
                        tooltipText={$t("Recently Purchased Tokens")}
                      >
                        RPT
                      </TooltipTitle>
                    </div>
                  </th>
                </tr>
              </thead>

              {#if $queryWhalesList.isFetching}
                <tbody>
                  <tr>
                    <td colspan="10">
                      <div
                        class="flex justify-center items-center h-screen py-3 px-3"
                      >
                        {#if isNimbusVersion}
                          <LoadingPremium />
                        {:else}
                          <Loading />
                        {/if}
                      </div>
                    </td>
                  </tr>
                </tbody>
              {:else}
                <tbody class="relative">
                  {#if whalesData && whalesData?.length === 0}
                    <tr>
                      <td colspan="10">
                        <div
                          class="flex justify-center items-center h-screen py-4 px-3 text-lg text-gray-400"
                        >
                          Empty
                        </div>
                      </td>
                    </tr>
                  {:else}
                    {#each (filterDuplicates(whalesData) || [])?.slice(0, $selectedPackage === "FREE" ? 10 : undefined) as data}
                      <PublicPortfolioItem {data} {whalePageChain} />
                    {/each}

                    {#if $selectedPackage === "FREE"}
                      <tr class="h-[200px]">
                        <td colspan="10">
                          {#if whalesData?.length > 5}
                            <tr
                              class={`absolute z-7 left-0 right-0 bottom-0 top-[270px] flex justify-center pt-10 backdrop-blur-md bg-gradient-to-t to-transparent ${
                                $isDarkMode
                                  ? "bg-black/90 from-[#000] via-[#222222]"
                                  : "bg-white/95 from-white via-white"
                              }`}
                            >
                              <td
                                colspan="10"
                                class="flex flex-col items-center gap-1"
                              >
                                {#if isNimbusVersion}
                                  <div class="text-lg font-medium">
                                    {$t("Use Nimbus")}
                                  </div>
                                {/if}
                                <div class="text-base text-gray-500">
                                  {$t("Upgrade to Premium")}
                                  <span class="font-medium"
                                    >{whalesData?.length - 5 > 0
                                      ? whalesData?.length - 5
                                      : ""}</span
                                  > whales
                                </div>
                                <div class="mt-2 w-max">
                                  <Button
                                    variant="premium"
                                    on:click={() => navigate("/upgrade")}
                                  >
                                    {$t("Upgrade Plan")}
                                  </Button>
                                </div>
                              </td>
                            </tr>
                          {:else}
                            <tr
                              class={`absolute z-7 left-0 right-0 bottom-0 top-[120px] flex justify-center pt-10 backdrop-blur-md bg-gradient-to-t to-transparent ${
                                $isDarkMode
                                  ? "bg-black/90 from-[#000] via-[#222222]"
                                  : "bg-white/95 from-white via-white"
                              }`}
                            >
                              <td
                                colspan="10"
                                class="flex flex-col items-center gap-1"
                              >
                                {#if isNimbusVersion}
                                  <div class="text-lg font-medium">
                                    {$t("Use Nimbus")}
                                  </div>
                                {/if}
                                <div class="text-base text-gray-500">
                                  {$t("Upgrade to Premium")}
                                  <span class="font-medium"
                                    >{whalesData?.length - 5 > 0
                                      ? whalesData?.length - 5
                                      : ""}</span
                                  > whales
                                </div>
                                <div class="mt-2 w-max">
                                  <Button
                                    variant="premium"
                                    on:click={() => navigate("/upgrade")}
                                  >
                                    {$t("Upgrade Plan")}
                                  </Button>
                                </div>
                              </td>
                            </tr>
                          {/if}
                        </td>
                      </tr>
                    {/if}
                  {/if}
                </tbody>
              {/if}
            </table>
          </span>

          <span slot="mobile">
            {#if $queryWhalesList.isFetching}
              <div class="flex justify-center items-center h-screen py-3 px-3">
                {#if isNimbusVersion}
                  <LoadingPremium />
                {:else}
                  <Loading />
                {/if}
              </div>
            {:else}
              <div class="relative w-full">
                {#if whalesData && whalesData?.length === 0}
                  <div
                    class="flex justify-center items-center h-screen py-3 px-3 text-lg text-gray-400"
                  >
                    {$t("Empty")}
                  </div>
                {:else}
                  {#each (filterDuplicates(whalesData) || [])?.slice(0, $selectedPackage === "FREE" ? 7 : undefined) as data, whalePosition}
                    <PublicPortfolioMobileItem {data} {whalePageChain} />
                  {/each}

                  {#if $selectedPackage === "FREE"}
                    {#if whalesData?.length > 5}
                      <div
                        class={`absolute left-0 bottom-0-0 z-7 w-full justify-center py-60 px-4 backdrop-blur-md bg-gradient-to-t to-transparent ${
                          $isDarkMode
                            ? "bg-black/90 from-[#000] via-[#222222]"
                            : "bg-white/95 from-white via-white"
                        }`}
                      >
                        <div class="flex flex-col items-center gap-1">
                          {#if isNimbusVersion}
                            <div class="text-lg font-medium">
                              {$t("Use Nimbus")}
                            </div>
                          {/if}
                          <div class="text-base text-gray-500">
                            {$t("Upgrade to Premium")}
                            <span class="font-medium"
                              >{whalesData?.length - 5 > 0
                                ? whalesData?.length - 5
                                : ""}</span
                            > whales
                          </div>
                          <div class="mt-2 w-max">
                            <Button
                              variant="premium"
                              on:click={() => navigate("/upgrade")}
                            >
                              {$t("Upgrade Plan")}
                            </Button>
                          </div>
                        </div>
                      </div>
                    {:else}
                      <div
                        class={`absolute left-0 right-0 z-7 bottom-0 top-[120px] flex justify-center pt-10 backdrop-blur-md bg-gradient-to-t to-transparent ${
                          $isDarkMode
                            ? "bg-black/90 from-[#000] via-[#222222]"
                            : "bg-white/95 from-white via-white"
                        }`}
                      >
                        <div class="flex flex-col items-center gap-1">
                          {#if isNimbusVersion}
                            <div class="text-lg font-medium">
                              {$t("Use Nimbus")}
                            </div>
                          {/if}
                          <div class="text-base text-gray-500">
                            {$t("Upgrade to Premium")}
                            <span class="font-medium"
                              >{whalesData?.length - 5 > 0
                                ? whalesData?.length - 5
                                : ""}</span
                            > whales
                          </div>
                          <div class="mt-2 w-max">
                            <Button
                              variant="premium"
                              on:click={() => navigate("/upgrade")}
                            >
                              {$t("Upgrade Plan")}
                            </Button>
                          </div>
                        </div>
                      </div>
                    {/if}
                  {/if}
                {/if}
              </div>
            {/if}
          </span>
        </Table>
      </div>

      {#if whalesData.length !== 0 && $selectedPackage !== "FREE"}
        <Pagination isDisabled={false} {totalPage} {handlePageChange} />
      {/if}
    </div>
  </div>
</ErrorBoundary>

<style>
  .header-container {
    background-repeat: no-repeat;
    background-size: auto;
    background-position: top right;
    padding-bottom: 144px;
    padding-top: 24px;
  }

  :global(body) .header-container {
    background-color: #27326f;
    background-image: url("~/assets/capa.svg");
  }
  :global(body.dark) .header-container {
    background-color: #080808;
    background-image: url("~/assets/capa-dark.svg");
  }

  :global(body) .virtual_portfolio_container {
    background: #fff;
    box-shadow: 0px 0px 40px 0px rgba(0, 0, 0, 0.1);
  }
  :global(body.dark) .virtual_portfolio_container {
    background: #0f0f0f;
    box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 1);
  }
</style>
